import { Component, OnInit } from '@angular/core';
import { CartRevistaService } from './../cart-revista.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-revista',
  templateUrl: './revista.page.html',
  styleUrls: ['./revista.page.scss'],
})
export class RevistaPage implements OnInit {

  cart = [];
  items = [];
  expanded = false;

  sliderConfig = {
    spaceBetween: 10,
    centeredSlides: true,
    //slidesPerView: 1.6 

  }


  constructor(private cartRevistaService: CartRevistaService, private router: Router, private actRoute: ActivatedRoute ) { }

  ngOnInit() {
    this.cart = this.cartRevistaService.getCart();
    this.items = this. cartRevistaService.getProductos();

  }

  addToCart(product) {
    this.cartRevistaService.addProduct(product);
  }

  openCart() {
    this.router.navigate(['cart-revista']);
  }

}
